import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { LoginService } from '@core/services/login.service';
import { faCalendarAlt } from '@fortawesome/free-regular-svg-icons';
import { NgbDate } from '@ng-bootstrap/ng-bootstrap';
import * as moment from 'moment';


@Component({
  selector: 'duelit-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  accountInfoForm: FormGroup;
  changePasswordForm: FormGroup;
  calendarIcon = faCalendarAlt;
  constructor(
    private loginService: LoginService,
    private fb: FormBuilder,
  ) { }

  ngOnInit(): void {
    const user = this.loginService.user;

    const birthDate = moment(user.birthDate);
    const ngbBirthDate = new NgbDate(birthDate.year(), birthDate.month() + 1, birthDate.day());


    this.accountInfoForm = this.fb.group({
      firstName: new FormControl(user.firstName),
      lastName: new FormControl(user.lastName),
      email: new FormControl(user.email),
      birthDate: new FormControl(ngbBirthDate),
    });

    this.changePasswordForm = this.fb.group({
      change: new FormControl(false),
      password: new FormControl(null, [Validators.minLength(8)]),
      confirm: new FormControl(null, [Validators.minLength(8)])
    });

    this.changePasswordForm.get('password').disable();
    this.changePasswordForm.get('confirm').disable();
    this.changePasswordForm.get('change').valueChanges.subscribe(value => {
      if (value) {
        this.changePasswordForm.get('password').enable();
        this.changePasswordForm.get('confirm').enable();
      } else {
        this.changePasswordForm.get('password').disable();
        this.changePasswordForm.get('confirm').disable();
      }
    });
  }


  isConfirmPasswordCorrect(): boolean {
    const passwords = this.changePasswordForm.value;
    return passwords.password === passwords.confirm;
  }
}
